.cursor {
  border-radius: 50%;
  height: 8rem;
  width: 8rem;
  left: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  z-index: 99999;
}
.cursor__before {
  background-color: rgba(255,255,255,0.2);
  height: 2.4rem;
  width: 2.4rem;
  mix-blend-mode: difference;
  margin: -0.8rem;
  border-radius: 50%;
  transition: transform 0.15s ease;
  will-change: transform;
}
.cursor__after {
  background-color: #fff;
  height: 0.8rem;
  width: 0.8rem;
  top: 0rem;
  left: 0rem;
  border-radius: 50%;
  position: absolute;
  transition: background-color 0.2s ease, height 0.2s ease, width 0.2s ease;
}
.cursor__text {
  display: inline-block;
  font-size: 1.1rem;
  font-weight: 700;
  line-height: 1.2;
  margin-left: 0.2rem;
  margin-top: 2.7rem;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  text-align: center;
  transition: opacity 0.2s ease;
  z-index: 50;
}
.cursor--active .cursor__before {
  height: 6.4rem;
  width: 6.4rem;
  margin: -2.4rem;
  opacity: 0.5;
}
.cursor--active .cursor__after {
  display: none;
}
