.h2--visible .blast {
  opacity: 1;
  transform: none;
}
.basic-animation {
  opacity: 0;
}
.case__section {
  margin-bottom: 8rem;
}
.case__img {
  margin-bottom: 3.2rem;
}
.case__img img {
  display: block;
  max-width: 100%;
}
.case__typography {
  margin-bottom: 1.6rem;
}
.colors {
  display: flex;
}
.colors .color {
  display: inline-block;
  margin-right: 1.6rem;
  height: 14.4rem;
  position: relative;
  width: 12rem;
}
.colors .color__name {
  bottom: 1.6rem;
  font-size: 1.2rem;
  left: 1.6rem;
  position: absolute;
}
.colors .color__name--gray {
  color: #aaa;
}
.index-wrapper {
  background-color: #000;
  color: #fff;
  height: 100vh;
  padding-bottom: 32rem;
}
.index-container {
  align-items: center;
  display: flex;
  height: 100vh;
}
.main-container {
  align-items: center;
  display: flex;
  height: 100vh;
  min-width: 48rem;
  left: 8.333333333333332%;
  position: fixed;
  top: 0;
  width: 33.33333333333333vw;
}
.main__text {
  font-size: 2.4rem;
  margin-bottom: 4.8rem;
}
.main__text p {
  line-height: 1.333333333333333;
  margin-bottom: 2.4rem;
  opacity: 1;
}
.main__desc {
  margin-bottom: 6.222222222222222vh;
}
.main__desc p {
  opacity: 0.5;
}
.me {
  align-items: center;
  display: flex;
  margin-bottom: 7.111111111111111vh;
}
.me__photo {
  margin-right: 2.4rem;
}
.me__photo img {
  display: block;
  transform: translateY(-5px);
  width: 6.4rem;
}
.me__name {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.2rem;
}
.me__phone {
  font-size: 1.4rem;
  opacity: 0.5;
}
.works-container {
  overflow: auto;
  right: 0;
  position: absolute;
  top: 0;
  width: 50%;
}
.works {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.works .work {
  height: 29.166666666666668vw;
  overflow: hidden;
  width: 50%;
}
.works .work__img {
  background-size: cover;
  background-position: top center;
  height: 100%;
  transition: all 0.3s ease;
  width: 100%;
}
.works .work--big {
  height: 33.33333333333333vw;
  width: 100%;
}
.works .work:hover .work__img {
  transform: scale(1.04);
}
.works__plate {
  background-color: #000;
  height: 100%;
  right: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.grid--container {
  max-width: 100%;
}
@media (max-height: 900px) {
  .main__desc {
    margin-bottom: 3.555555555555555vh;
  }
  .me {
    margin-bottom: 3.555555555555555vh;
  }
}
@media (max-width: 1200px) {
  body.body--cursor-pointer {
    cursor: pointer;
  }
  .cursor {
    display: none;
  }
  .index-container {
    height: initial;
    margin-top: 10vh;
    margin-bottom: 8vh;
  }
  .main-container {
    height: initial;
    min-width: initial;
    position: initial;
    padding: 0 6%;
    width: 100%;
  }
  .works-container {
    position: initial;
    width: 100%;
  }
  .works .work {
    height: 60vw;
  }
  .works .work__img {
    background-position: center;
  }
}
@media (max-width: 700px) {
  .index-container {
    margin-top: 2rem;
  }
  .main__text {
    font-size: 2rem;
    margin-bottom: 2.4rem;
  }
  .main__desc {
    font-size: 1.2rem;
  }
}
